* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

:root {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
}

img {
  display: block;
  width: 100%;
}

.bg-body-start {
  background-image: url(../pic/imgsfondo2.webp);
}

.bg-body-chess {
  background-image: url(../pic/sfondo-legno.webp);
}

.bg-black {
  background-color: #000;
}

.turnColor {
  color: green;
  transform: scale(110%);
}

.containerStart {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.startGiocatori {
  width: 100%;
  margin: 0 20px;
  color: white;
  background-image: url(../pic/sfondo-legno.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.startGiocatori h2 {
  width: 100%;
  margin: 10px 0 20px 0;
  text-align: center;
}
.startGiocatori .giocatore {
  padding: 10px;
}
.startGiocatori .giocatore img {
  width: 80px;
  height: 80px;
  margin: 0 auto;
}
.startGiocatori label {
  display: block;
  margin: 15px 0;
  font-weight: 700;
  text-align: center;
}
.startGiocatori input {
  width: 80%;
  display: block;
  margin: 5px auto;
  padding: 8px 10px;
  border-radius: 5px;
  border: none;
}
.startGiocatori button {
  display: block;
  width: 40%;
  margin: 15px auto 10px;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  background-color: white;
  font-weight: 700;
}

