main {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-image: url(../pic/sfondo-legno.webp);
}

#timer {
  width: 100%;
  padding: 10px 0;
  text-align: center;
  color: white;
  font-weight: 800;
  font-size: 1.8rem;
}
#timer p {
  margin: 0;
}

.pedineMangiate {
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  padding: 20px 0;
}
.pedineMangiate h3 {
  margin: 5px 0;
  text-align: center;
}
.pedineMangiate section div {
  border: solid 2px white;
  width: 60%;
  height: 30px;
  margin: 0 auto;
}

