.containerChessboard {
  padding: 10px;
}

.chessboard {
  width: 355px;
  height: 355px;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  background-color: white;
}
.chessboard .square {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5%;
}

.white {
  background-color: white;
}

.black {
  background-color: #000;
}

.valid {
  background-color: rgba(125, 255, 38, 0.8);
}

.no-valid {
  background-color: rgba(255, 0, 0, 0.8);
}

.vittoria {
  padding: 10px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.vittoria h2 {
  text-align: center;
}

.vittoria span {
  margin: 20px;
  display: block;
  text-align: center;
  font-size: 1.3rem;
  font-weight: 800;
}

.vittoria button {
  display: block;
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: white;
  font-weight: 700;
}

.vittoria img {
  width: 80px;
  height: 80px;
}

#C-1, #C-2 {
  display: flex;
  gap: 0.15em;
  padding: 0.2em;
  background-color: rgba(255, 255, 255, 0.6156862745);
}

.deathToken {
  display: block;
  width: 8.3333333333%;
  height: 100%;
}

@media screen and (min-width: 576px) {
  .startGiocatori {
    width: 60%;
    margin: 0;
  }
  .pedineMangiate div {
    width: 50%;
  }
  .containerChessboard {
    padding: 20px;
  }
  .chessboard {
    width: 500px;
    height: 500px;
  }
}
@media screen and (min-width: 768px) {
  .startGiocatori {
    width: 50%;
  }
  .chessboard {
    width: 550px;
    height: 550px;
  }
}
@media screen and (min-width: 992px) {
  .startGiocatori {
    width: 40%;
  }
  .wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .pedineMangiate {
    width: 150px;
    display: flex;
    align-items: center;
  }
  #uno, #due {
    width: 100%;
    margin: 20px 0;
  }
  .pedineMangiate section {
    width: 100%;
  }
  .pedineMangiate section div {
    width: 30%;
    height: 350px;
  }
  .chessboard {
    width: 550px;
    height: 550px;
  }
  #C-1 {
    flex-direction: column;
  }
  #C-2 {
    flex-direction: column-reverse;
  }
  .deathToken {
    display: block;
    width: 100%;
    height: 8.3333333333%;
  }
}
@media screen and (min-width: 1200px) {
  .startGiocatori {
    width: 30%;
  }
  .pedineMangiate {
    width: 200px;
  }
}